import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "soQty" }),
        dataIndex: "ordered_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "soBaseQty" }),
        dataIndex: "base_ordered_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "deliveredQty" }),
        dataIndex: "delivered_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "leftQty" }),
        dataIndex: "left_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "deliveryQty" }),
        dataIndex: "delivery_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "baseQty" }),
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="salesOrders.customerPOInfo.no" />,
        description: data.customer_po_no ? data.customer_po_no : "-"
      },
      {
        label: <IntlMessages id="salesOrders.customerPOInfo.date" />,
        description: data.customer_po_date
          ? moment(data.customer_po_date, "YYYY-MM-DD").format("DD MMMM YYYY")
          : "-"
      },
      {
        label: <IntlMessages id="customers.tab.contact" />,
        description: data.contact_person ? data.contact_person : "-"
      },
      {
        label: <IntlMessages id="phone" />,
        description: data.phone ? data.phone : "-"
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location_name
      },
      {
        label: <IntlMessages id="salesman" />,
        description: data.salesman_name ? data.salesman_name : "-"
      },
      {
        label: <IntlMessages id="salesOrders.shipTo" />,
        description: data.ship_to ? data.ship_to : "-"
      }
    ];

    const total = [
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_qty} />
        // parseAmount: true
      },
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalReceiveQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_delivery_qty} />
        // parseAmount: true
      },
      {
        label: (
          <>
            <IntlMessages id="purchaseReceipts.totalLeftQty" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_left_qty} />
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      },
      {
        label: <IntlMessages id="salesOrders.receivedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.deliveryOrder" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.delivery_order_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.delivery_order_date
                        ? moment(
                            data.delivery_order_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.customer" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.customer ? data.customer.customer_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.delivery_order_details}
                rowKey={row => row.id_delivery_order_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "delivery_order",
              data.delivery_order_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
