import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";

import IntlMessages from "../../../../components/utility/intlMessages";
import actions from "../../../../redux/sales/deliveryOrder/actions";
import Button from "../../../../components/uielements/button";
import {
  AutoComplete,
  Button as ButtonAntd,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  // InputNumber,
  Select,
  Steps,
  Popconfirm,
  Row,
  Tabs,
  Tooltip
} from "antd";

import EditableTable from "./editableTable";
import DeliveryOrderDocUpdateForm from "./updateDocForm";
import ModalTransaction from "./modal";
import ModalCancel from "./modalCancel";
import ModalReject from "./modalReject";

// import { Object } from "core-js";
import { customDot, StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;
const TabPane = Tabs.TabPane;

const {
  setPrintData,
  confirmDeliveryOrder,
  rejectDeliveryOrder,
  cancelDeliveryOrder
} = actions;

class DeliveryOrderUpdateForm extends Component {
  state = {
    customerContactsName: [],
    customer: null,
    delivery_order_date: null,
    err: false,
    errTotDelQty: false,
    exceededItems: [],
    id_location: null,
    id_customer: null,
    id_sales_order: null,
    items: [],
    itemDetails: {
      delivery_order_details: []
    },
    total_qty: 0,
    total_delivery_qty: 0,
    total_left_qty: 0,
    limit: 0,
    salesOrder: {},
    search: null,
    tab: "1",
    visible: false,
    visibleModalCancel: false,
    visibleModalReject: false
  };

  componentDidMount = () => {
    const { info } = this.props.deliveryOrder;
    // when ordered qty is exceeded 
    if (info) {
      this.setState({ exceededItems: info });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const { deliveryOrder, loading } = this.props.deliveryOrder;
    const { itemDetails } = this.state;
    const prevDeliveryOrder = prevProps.deliveryOrder.deliveryOrder;
    const { customer } = deliveryOrder;
    if (!loading && prevDeliveryOrder !== deliveryOrder) {
      if (customer.customer_contacts.length > 0) {
        const customerFindContact = [];
        for (var i = 0; i < customer.customer_contacts.length; i++) {
          customerFindContact.push(
            customer.customer_contacts[i].customer_contact_name
          );
        }
        this.setState({
          customerContactsName: customerFindContact
          // customerContacts: customer.customer_contacts
        });
      } else {
        this.setState({ customerContactsName: [] });
      }
      this.setState({
        customer: customer,
        delivery_order_date: deliveryOrder.delivery_order_date,
        id_sales_order: deliveryOrder.id_sales_order,
        items: deliveryOrder ? deliveryOrder.delivery_order_details : [],
        status: deliveryOrder.status
      });
    } else if (
      !loading &&
      prevState.itemDetails.delivery_order_details.length === 0 &&
      prevDeliveryOrder === deliveryOrder &&
      this.state.limit === 0
    ) {
      if (customer.customer_contacts.length > 0) {
        const customerFindContact = [];
        for (var i = 0; i < customer.customer_contacts.length; i++) {
          customerFindContact.push(
            customer.customer_contacts[i].customer_contact_name
          );
        }
        this.setState({
          customerContactsName: customerFindContact
          // customerContacts: customer.customer_contacts
        });
      } else {
        this.setState({ customerContactsName: [] });
      }
      this.setState({
        customer: customer,
        delivery_order_date: deliveryOrder.delivery_order_date,
        id_sales_order: deliveryOrder.id_sales_order,
        items: deliveryOrder ? deliveryOrder.delivery_order_details : [],
        limit: 1,
        status: deliveryOrder.status
      });
    }

    if (prevState.itemDetails.delivery_order_details !== itemDetails.delivery_order_details) {
      const { setPrintData } = this.props;
      const { total_qty, total_delivery_qty, total_left_qty } = this.state;
      const total = { total_qty, total_delivery_qty, total_left_qty };
      setPrintData({ ...itemDetails,  ...total});
    }
  }

  handleCustomerContact = value => {
    const { customerContactsName } = this.state;
    const { customer } = this.state;
    const { setFieldsValue } = this.props.form;
    if (customer && customerContactsName.length > 0) {
      const findCustCont = customer.customer_contacts.find(
        data => data.customer_contact_name === value
      );
      if (findCustCont) {
        setFieldsValue({
          phone: findCustCont.phone
        });
      } else {
        setFieldsValue({
          phone: null
        });
      }
    } else {
      setFieldsValue({
        phone: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (err) {
        this.setState({ err: true });
        return;
      }
      const { 
        itemDetails, 
        id_sales_order, 
        total_qty, 
        total_delivery_qty, 
        total_left_qty 
      } = this.state;

      values.delivery_order_date = values.delivery_order_date
        ? values.delivery_order_date.format("YYYY-MM-DD HH:mm:ss")
        : this.state.delivery_order_date;

      const data = Object.assign({}, values, itemDetails, {
        id_sales_order: id_sales_order,
        total_qty: total_qty,
        total_delivery_qty: total_delivery_qty,
        total_left_qty: total_left_qty
      });
      if (parseFloat(data.total_delivery_qty) === 0) {
        this.setState({ errTotDelQty: true });
        return;
      }
      this.setState({ errTotDelQty: false });
      if (!err) {
        // console.log("submitted data", data);
        this.props.submit(data);
      }
    });
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectModal = value => {
    const { transactions } = this.props.deliveryOrder;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_sales_order === value
    );
    const { customer } = transaction;
    this.setState({
      customer,
      customer_name: transaction.customer_name,
      id_sales_order: transaction.id_sales_order,
      id_location: transaction.id_location,
      items: transaction.sales_order_details,
      itemDetails: {
        delivery_order_details: transaction.sales_order_details
      }
    });
    if (customer.customer_contacts.length > 0) {
      const customerFindContact = [];
      for (var i = 0; i < customer.customer_contacts.length; i++) {
        customerFindContact.push(
          customer.customer_contacts[i].customer_contact_name
        );
      }
      this.setState({
        customerContactsName: customerFindContact
        // customerContacts: customer.customer_contacts
      });
    } else {
      this.setState({ customerContactsName: [], customer });
    }
    setFieldsValue({
      address_name: [],
      contact_person: transaction.contact_person
        ? transaction.contact_person
        : null,
      customer_name: transaction.customer_name,
      customer_po_no: transaction.customer_po_no
        ? transaction.customer_po_no
        : "-",
      customer_po_date: transaction.customer_po_date
        ? moment(transaction.customer_po_date, "YYYY-MM-DD")
        : null,
      id_location: transaction.id_location,
      phone: transaction.phone ? transaction.phone : null,
      salesman_name: transaction.salesman_name
        ? transaction.salesman_name
        : "-",
      sales_order_no: transaction.sales_order_no,
      ship_to: transaction.ship_to ? transaction.ship_to : "",
      id_courier: transaction.id_courier
    });
    this.handleModal();
  };

  handleSelectSupplier = value => {
    this.setState({ id_customer: value });
  };

  handleSelectLocation = value => {
    this.setState({ id_location: value });
  };

  handleSaveItemList = (items, total_qty, total_received, total_left) => {
    if (parseFloat(total_received) > 0) {
      this.setState({
        itemDetails: {
          delivery_order_details: items
        },
        total_qty: total_qty,
        total_delivery_qty: total_received,
        total_left_qty: total_left,
        errTotDelQty: false
      });
    } else {
      this.setState({
        itemDetails: {
          delivery_order_details: items
        },
        total_qty: total_qty,
        total_delivery_qty: total_received,
        total_left_qty: total_left,
      });
    }
  };

  clearItems = (items, total_qty, total_received, total_left) => {
    this.setState({
      items: [],
      itemDetails: {
        delivery_order_details: items
      },
      total_qty: total_qty,
      total_delivery_qty: total_received,
      total_left_qty: total_left,
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  handleModalReject = () => {
    this.setState({
      visibleModalReject: !this.state.visibleModalReject
    });
  };

  renderAddress = id_address => {
    const { setFieldsValue } = this.props.form;
    const customerAddresses = this.state.customer
      ? this.state.customer.customer_addresses
      : null;
    const findAdd = customerAddresses.find(
      data => data.id_customer_address === id_address
    );
    var string =
      findAdd.customer_address_name +
      " - " +
      findAdd.address +
      (findAdd.district ? ", " + findAdd.district.district_name + ", " : "") +
      (findAdd.city
        ? findAdd.city.city_type + " " + findAdd.city.city_name + ", "
        : "") +
      (findAdd.province ? findAdd.province.province_name + ", " : "") +
      (findAdd.country ? findAdd.country.country_name : "");
    if (findAdd.zip_code) {
      string += " " + findAdd.zip_code;
    }
    setFieldsValue({ ship_to: string });
  };

  render() {
    // console.log(this.state.itemDetails);
    // const { visible } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const customerAddresses = this.state.customer
      ? this.state.customer.customer_addresses
      : null;
    const { deliveryOrder } = this.props;
    const { locations, couriers } = deliveryOrder ? deliveryOrder : [];
    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingCancel,
      loadingConfirm,
      loadingReject,
      loadingSubmit,
      loadingTransactions,
      loadingForeign
    } = deliveryOrder ? deliveryOrder : [];
    const data = deliveryOrder.deliveryOrder ? deliveryOrder.deliveryOrder : [];

    // const used = data.is_used;
    // const error = deliveryOrder
    //   ? deliveryOrder.errorField
    //     ? deliveryOrder.errorField
    //     : []
    //   : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          deliveryOrder={this.props.deliveryOrder}
          id_location={getFieldValue("id_location")}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
          visible={this.state.visible}
        />

        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loadingCancel}
        />

        <ModalReject
          visible={this.state.visibleModalReject}
          handleModal={this.handleModalReject}
          submit={this.props.handleSubmitReject}
          loading={loadingReject}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps
              size="small"
              progressDot={customDot}
              current={data.status - 1}
            >
              <Step title={getLanguage({ id: "new" })} />
              <Step title={getLanguage({ id: "approved" })} />
              <Step title={getLanguage({ id: "rejected" })} />
              <Step title={getLanguage({ id: "invoiced" })} />
              <Step title={getLanguage({ id: "cancelled" })} />
            </Steps>
          </Col>
        </Row>

        <Tabs
          defaultActiveKey="1"
          onTabClick={key => this.setState({ tab: key })}
        >
          <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="deliveryOrders.no" />}
                      >
                        {getFieldDecorator("delivery_order_no", {
                          initialValue: data.delivery_order_no
                        })(<Input disabled={true} />)}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesOrders.deliveryDate" />}
                        style={{ marginBottom: 0, paddingBottom: 0 }}
                      >
                        {getFieldDecorator("delivery_order_date", {
                          initialValue: data.delivery_order_date
                            ? moment(
                                data.delivery_order_date,
                                "YYYY-MM-DD HH:mm:ss"
                              )
                            : moment(),
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            // onChange={this.handleChangeTransDate}
                            onOpenChange={this.handleChangeTransDate}
                            disabled={true}
                            allowClear={false}
                            disabledDate={e => e.valueOf() > moment()}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={<IntlMessages id="deliveryOrders.location" />}
                          >
                            <IntlMessages id="locations.pageHeader" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: data.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={this.state.status !== 1 ? true : false}
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseOrders.filter.select.location" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleSelectLocation}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations && locations.length > 0
                              ? locations.map((data, key) => (
                                  <Option key={key} value={data.id_location}>
                                    {`${data.location_code} (${
                                      data.location_name
                                    })`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={<IntlMessages id="deliveryOrders.no.help" />}
                          >
                            <IntlMessages id="deliveryOrders.salesOrder" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                         <Button
                          disabled={loadingTransactions}
                          icon="select"
                          loading={loadingTransactions}
                          onClick={this.handleModal}
                          style={{ height: "31px", width: "100%" }}
                          disabled={this.state.status !== 1 ? true : false}
                          // shape="circle"
                        >
                          <IntlMessages id="button.import" />
                        </Button>
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Form.Item
                  {...formTextArea}
                  label={<IntlMessages id="purchaseReceipts.note" />}
                >
                  {getFieldDecorator("note", {
                    initialValue: data.note
                  })(
                    <TextArea
                      disabled={this.state.status !== 1 ? true : false}
                      autosize={{ minRows: 4 }}
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>

            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesOrders.customer" />}
                    >
                      {getFieldDecorator("customer_name", {
                        initialValue: data.customer_name,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Input
                          disabled
                          // placeholder="Select a Sales Order No."
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesman" />}
                    >
                      {getFieldDecorator("salesman_name", {
                        initialValue: data.salesman_name
                          ? data.salesman_name
                          : null
                      })(
                        <Input
                          disabled
                          // placeholder="Select a Sales Order No."
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesOrders.salesOrderNo" />}
                    >
                      {getFieldDecorator("sales_order_no", {
                        initialValue: data.sales_order_no,
                      })(
                        <Input disabled={true} />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
            </Row>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="itemDetails" />
            </Divider>

            <Row style={{ marginBottom: "30px" }}>
              <Col span={24}>
                <EditableTable
                  clearItems={this.clearItems}
                  handleSaveItemList={this.handleSaveItemList}
                  items={this.state.items}
                  loadingTransactions={loadingTransactions}
                  status={this.state.status}
                  exceededItems={this.state.exceededItems}
                />
              </Col>
            </Row>

            <Row>
              <Col span={14} offset={10} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                    <IntlMessages id="purchaseReceipts.totalQty" />
                    <span>
                      <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                    </span>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_qty} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                    <IntlMessages id="totalDeliveryQty" />
                    <span>
                      <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                    </span>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_delivery_qty} />
                    </span>
                  </Col>

                  {/* Error total received qty */}
                  {this.state.errTotDelQty ? (
                    <Col
                      span={15}
                      pull={9}
                      style={{ textAlign: "right", marginTop: "-5px" }}
                    >
                      <span style={{ color: "#F5212D" }}>
                        <IntlMessages id="salesOrders.totalDeliveryQty.error" />
                        <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                      </span>
                    </Col>
                  ) : null}
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
                    <strong>
                      <IntlMessages id="purchaseReceipts.totalLeftQty" />
                      <span>
                        <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                      </span>
                    </strong>
                  </Col>
                  <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                    <span>
                      <NumberFormat value={this.state.total_left_qty} />
                    </span>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane tab={<IntlMessages id="infoAndShipment" />} key="2">
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="customerInfo" />
                </Divider>
                <Row>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="salesOrders.customerPOInfo.no" />
                      }
                    >
                      {getFieldDecorator("customer_po_no", {
                        initialValue: data.customer_po_no
                          ? data.customer_po_no
                          : null
                      })(<Input disabled style={{ width: "100%" }} />)}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="salesOrders.customerPOInfo.date" />
                      }
                    >
                      {getFieldDecorator("customer_po_date", {
                        initialValue: data.customer_po_date
                          ? moment(data.customer_po_date, "YYYY-MM-DD")
                          : null
                      })(
                        <DatePicker
                          disabled
                          format="DD MMMM YYYY"
                          placeholder=""
                          style={{ width: "100%" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12} style={{ paddingRight: "8px" }}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="customers.tab.contact" />}
                    >
                      {getFieldDecorator("contact_person", {
                        initialValue: data.contact_person
                      })(
                        <AutoComplete
                          disabled={this.state.status !== 1 ? true : false}
                          style={{ width: "100%" }}
                          dataSource={this.state.customerContactsName}
                          placeholder={<IntlMessages id="contactPerson" />}
                          filterOption={(inputValue, option) =>
                            option.props.children
                              .toUpperCase()
                              .indexOf(inputValue.toUpperCase()) !== -1
                          }
                          onBlur={this.handleCustomerContact}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="phone" />}
                    >
                      {getFieldDecorator("phone", {
                        initialValue: data.phone
                      })(
                        <Input
                          placeholder={getLanguage({ id: "phone" })}
                          disabled={this.state.status !== 1 ? true : false}
                          style={{ width: "100%" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.shipment" />
                </Divider>
                <Row>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={<IntlMessages id="address.select.help" />}
                          >
                            <IntlMessages id="address.select" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("address_name", {
                          // initialValue: data.id_payment_term
                        })(
                          <Select
                            disabled={this.state.status !== 1 ? true : false}
                            showSearch
                            style={{ width: "100%" }}
                            onSelect={this.renderAddress}
                            placeholder={<IntlMessages id="address.select" />}
                            loading={loadingForeign}
                            optionFilterProp="children"
                            filterOption={true}
                            notFoundContent={
                              this.state.id_sales_order ? (
                                <Empty
                                  description={
                                    <IntlMessages id="customers.noAddress" />
                                  }
                                />
                              ) : (
                                <Empty
                                  description={
                                    <IntlMessages id="salesOrders.selectFirst" />
                                  }
                                />
                              )
                            }
                          >
                            {customerAddresses && customerAddresses.length > 0
                              ? customerAddresses.map((data, key) => (
                                  <Option
                                    key={key}
                                    value={data.id_customer_address}
                                  >
                                    {data.address} ({""}
                                    {data.customer_address_name}
                                    {""})
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="itemTransfers.courier" />}
                      >
                        {getFieldDecorator("id_courier", {
                          initialValue: data.id_courier,
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={<IntlMessages id="itemTransfers.courier" />}
                            loading={loadingForeign}
                            optionFilterProp="children"
                            filterOption={true}
                            disabled={true}
                          >
                            {couriers.map((data, key) => (
                              <Option key={key} value={data.id_courier}>
                                {`${data.courier_name} (${data.description})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                  <Col span={24}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="salesOrders.shipTo" />}
                    >
                      {getFieldDecorator("ship_to", {
                        initialValue: data.ship_to
                      })(
                        <TextArea
                          autosize={{ minRows: 4 }}
                          disabled={this.state.status !== 1 ? true : false}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            key="3"
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
          >
            <DeliveryOrderDocUpdateForm
              submit={this.handleSubmit}
              deliveryOrder={this.props.deliveryOrder}
              params={this.props.params}
              form={this.props.form}
            />
          </TabPane>
        </Tabs>

        {this.state.tab !== "3" ? (
          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginRight: "5px", marginTop: "24px" }}
              loading={loading || loadingSubmit}
              disabled={
                this.state.errTotDelQty ||
                !permissions.includes("Update Delivery Order") ||
                data.status !== 1
              }
            >
              <IntlMessages id="button.submit" />
            </Button>

            {/* Confirm */}
            <Popconfirm
              title={<IntlMessages id="confirmTransaction" />}
              onConfirm={() =>
                this.props.confirmDeliveryOrder(data.id_delivery_order)
              }
              okText={<IntlMessages id="yes" />}
              cancelText={<IntlMessages id="no" />}
            >
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.status !== 1 ||
                  !permissions.includes("Confirm Delivery Order")
                    ? true
                    : false
                }
                loading={loadingConfirm}
              >
                <IntlMessages id="button.confirmTransaction" />
              </Button>
            </Popconfirm>

            {/* Reject */}
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 1 ||
                !permissions.includes("Confirm Delivery Order")
                  ? true
                  : false
              }
              loading={loadingReject}
              onClick={this.handleModalReject}
            >
              <IntlMessages id="button.rejectTransaction" />
            </Button>

            {/* Cancel */}
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 2 ||
                !permissions.includes("Cancel Delivery Order")
                  ? true
                  : false
              }
              loading={loadingCancel}
              onClick={this.handleModalCancel}
            >
              <IntlMessages id="button.cancelTransaction" />
            </Button>
          </Form.Item>
        ) : null}

      </Form>
    );
  }
}

const WrappedDeliveryOrderUpdateForm = Form.create({
  name: "update_delivery_order"
})(DeliveryOrderUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    { setPrintData, confirmDeliveryOrder, rejectDeliveryOrder, cancelDeliveryOrder }
  )
)(WrappedDeliveryOrderUpdateForm);
