import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import DeliveryOrderUpdateForm from "./updateForm";
import HistoryPurchaseReceipt from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Print from "./print";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/deliveryOrder/actions";
import { Box } from "../../../App/app.style";

const redirecTo = "/dashboard/sales/delivery-orders";

class UpdateDeliveryOrder extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editDeliveryOrder } = this.props;
    const { id } = this.props.match.params;
    editDeliveryOrder(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.deliveryOrder.error) {
      notification("error", this.props.deliveryOrder.error);
      this.props.clearError();
    }
    if (this.props.deliveryOrder.success) {
      notification("success", this.props.deliveryOrder.success);
      this.props.clearSuccess();
    }
    if (this.props.deliveryOrder.warning) {
      notification("warning", this.props.deliveryOrder.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateDeliveryOrder } = this.props;
    const { id } = this.props.match.params;
    updateDeliveryOrder(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelDeliveryOrder } = this.props;
    const { id } = this.props.match.params;

    cancelDeliveryOrder(id, value);
  };

  handleSubmitReject = value => {
    const { rejectDeliveryOrder } = this.props;
    const { id } = this.props.match.params;

    rejectDeliveryOrder(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, deliveryOrder } = this.props.deliveryOrder;
    const { printData } = this.props.deliveryOrder;
    const modified = { ...deliveryOrder, ...printData };
    
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="deliveryOrders.update" />}>
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              onClick={() => this.props.history.replace(redirecTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPurchaseReceipt
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={deliveryOrder ? deliveryOrder.description : null}
          />

          {this.props.deliveryOrder.loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <DeliveryOrderUpdateForm
              deliveryOrder={this.props.deliveryOrder}
              handleSubmitCancel={this.handleSubmitCancel}
              handleSubmitReject={this.handleSubmitReject}
              submit={this.handleSubmit}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    deliveryOrder: state.deliveryOrder,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateDeliveryOrder);
